<template>
  <div class="affirmsat">
    <router-link to="/chooseSat"><span class="jt"><</span></router-link>
    <h2 class="sHead">确认订单</h2>

    <div class="card">
      <div class="cardbg">
        <h3>战狼2</h3>
        <span>{{pages}}张</span>
        <p>明天2017-09-06 20:10:00(周二晚上)(国语 2D)</p>
        <p>北京古城电影院</p>
        <p>4号厅6排4座</p>
        <div>
          <p>付款时间</p>
          <p>15:00</p>
        </div>

        <p>开场前1小时可退票(有退票费)</p>
        <p>开场前1小时可改签(有改签费)</p>
      </div>
    </div>

    <div class="tprice">
      <h3>票价总计</h3>
      <p>含服务费3元/张<span>33元</span></p>
    </div>

    <div class="infor">
      <h3>购票和退改签须知</h3>
      <h4>退改签说明</h4>
      <ul>
        <li>未取票开场前24小时，可免费退票</li>
        <li>未取票开场前1小时，可改签，改签费规则<span>点击查看</span></li>
      </ul>
      <h4>购票须知</h4>
      <ol>
        <li>1. fdddddddddd</li>
        <li>2. dddddddd</li>
        <li>3. dddddddddddd</li>
      </ol>
    </div>

    <div class="fk">
      <p>应付<span>{{pages * 36}}元</span></p>
      <div @click="fk">立即付款</div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'affirmsat',
    data () {
      return {
        pages: ''
      }
    },
    methods: {
      fk: function (ev) {
        this.$message({
          message: '暂未开通付款',
          type: 'warning'
        })
      }
    },
    created () {
      console.log(window.sessionStorage.pages)
      this.pages = window.sessionStorage.pages
      window.sessionStorage.pages = 0
    },
    mounted: function () {
    }
  }
</script>
<style>

  .affirmsat{
    text-align: left;
  }
  .affirmsat h2{
    text-align: center;
  }
  .affirmsat .card{
    text-align: left;
    padding:15px 0;
    background:#aaaaaa;
  }
  .fk{
    width:100%;
    border-top:1px solid gainsboro;
    position:fixed;
    bottom:0;
    text-align: right;
  }
  .fk p, .fk div{
    text-align: center;
     display:inline-block;
  }
  .fk p{
    float:left;
    line-height:55px;
    padding-left:10px;
  }
  .fk p span{
    color:#ff4d64;
    font-weight:bolder;
    font-size:20px;
  }
  .fk div{
    width:35%;
    background:#ff4d64;
    color:white;
    line-height:55px;
  }
  .affirmsat .cardbg{
    position:relative;
    border-radius:7px;
    margin-left:5%;
    width:85%;
    background:white;
    padding:10px;
    color:dimgrey;
    font-size:13px;
  }
  .affirmsat h3{
    color:black;
    font-weight: normal;
    font-size:16px;
  }
  .affirmsat .cardbg h3{
    display:inline-block;
    padding-bottom: 7px;
  }
  .affirmsat .cardbg p{
    line-height:24px;
  }
  .affirmsat .cardbg p:nth-child(5){
    padding-bottom:10px;
    margin-bottom:10px;
    border-bottom:1px dotted #aaaaaa;
  }
  .affirmsat .cardbg div{
    position: absolute;
    top:10px;
    right:10px;
    width:60px;
  }
  .affirmsat .cardbg div p{
     text-align: right;
   }

  .affirmsat .tprice{
    border-bottom:7px solid gainsboro;
    padding-left:5%;
    height:40px;
  }
  .affirmsat .tprice p,.affirmsat .tprice h3{
    position:absolute;
    line-height:40px;
  }
  .affirmsat .tprice p{
    right:5%;
  }
  .affirmsat .tprice p span{
    color:#ff4d64;
    font-weight:bolder;
  }
  .affirmsat .infor{
    line-height:30px;
    font-size:13px;
    color:dimgrey;
    /*border:1px solid blue;*/
    width:90%;
    margin:0 0 60px 5%;
  }
  .affirmsat .infor h3{
    line-height:45px;
    border-bottom:1px solid gainsboro;
  }
  .affirmsat .infor h4{
    font-weight:normal;
    color:black;
    padding-top:10px;
    font-size:16px;
  }
</style>



