<template>
    <ul>
      <li v-for="(dataEle,index) in datas">
        <router-link :to="{path:'cinemaInfo/'+index}">
        <div class="cinemaList">
          <h3>{{dataEle.cinemaName}}</h3>
          <div class="price">
            <span>
              {{dataEle.noShowDisplayPrice/100}}
            </span>元起
          </div>
          <p class="hid">{{dataEle.address}}</p>
          <button v-for="a in dataEle.displaySupports">{{a.name}}</button>

          <p><span class="tehui">特</span>特惠</p>
          <p>场次：</p>
        </div>
        </router-link>
      </li>
    </ul>
</template>
<script>
  import Axios from 'axios'

  export default {
    name: 'cinemaList',
    data () {
      return {
        datas: ''
      }
    },
    created () {
      // this.$http.get
      Axios.get('./static/data/cinema.json')
        .then((res) => {
          console.log(res.data.data.returnValue.mtopCinemas[0])
          this.datas = res.data.data.returnValue.mtopCinemas
        })
    }
  }
</script>
<style>
  .cinemaList{
    text-align: left;
    margin-left:3%;
    border-bottom: 1px solid lightgray;
    padding-bottom: 10px;
  }
  .cinemaList h3{
    display:inline-block;
    margin:5px 0;
    width:45%;
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
  }
  .cinemaList p{
    margin:0;
    font-size:15px;
    color:gray;
  }
  .cinemaList p:nth-child(3){
    width:90%;
  }
  .cinemaList span{
    color:red;
    font-weight:bold;
    font-size:19px;
  }
  .price{
    display:inline-block;
    position: relative;
    top:-7px;
  }
  .cinemaList button{
    width:50px;
    outline:none;
    height:20px;
    background:none;
    border:1px solid #90b4fc;
    border-radius: 10px;
    color:#90b4fc;
    margin-top:4px;
  }
  p .tehui{
    display:inline-block;
    background:red;
    border:2px solid red;
    border-radius: 4px;
    color:white;
    margin:5px 5px 5px 0;
    font-size:15px;
  }
</style>



