<template>
  <div class="chooseSat">
    <div class="head">

      <router-link to="/cinema">
        <span class="jt"><</span>
      </router-link>

      <h5>{{datas.cinemaName}}</h5>
      <p>今天09-06-16:26(国语2D)</p>
    </div>

    <div class="sat">
      <p>4号厅</p>
      <ul>
        <li>可选 <span></span></li>
        <li>已售 <span></span></li>
        <li>已选 <span></span></li>
      </ul>
      <div id="seatList">
        <div class="seatItem">
          <span v-for="a in 8"><br>
              <span v-for="b in 6" @click="choose">
                {{a}}排<br />{{b}}座
              </span>
          </span>
        </div>
      </div>
    </div>

    <div class="showOne" v-bind:class="{ show: isActive }">
      <p>购物成功后将发送取票码到</p>

      <div class="showFour" v-bind:class="{ show: isActive2 }">
        <p>{{phone}}</p>
        <img src="../assets/write.png" alt="" @click="write">
      </div>

      <div class="showThree" v-bind:class="{ show: !isActive2 }">
        <input type="text" :value=phone>
        <button @click="write">确定</button>
      </div>

      <p>票价<span class="priceSeat">{{price}}</span>元</p>
      <router-link to="/affirmSat">
        <div class="buttonActive">确认选座</div>
      </router-link>
    </div>

    <div class="showTwo" v-bind:class="{ show: !isActive }">
      <div class="buyBtn">请在上图中选择座位</div>
    </div>
  </div>
</template>
<script>
  import Axios from 'axios'

  export default {
    name: 'chooseSat',
    data () {
      return {
        isActive3: 'true',
        datas: '',
        isActive: false,
        isActive2: false,
        phone: '15321654926',
        price: 0
      }
    },
    methods: {
      choose: function (ev) {
//        this.isActive3 = !this.isActive3
        if (ev.target.style.background === 'green') {
          ev.target.style.background = 'white'
          this.price = this.price - 33
          window.sessionStorage.pages --
        } else {
          ev.target.style.background = 'green'
          this.price = this.price + 33
          window.sessionStorage.pages ++
        }
        document.getElementsByClassName('priceSeat')[0].innerHTML = this.price
      },
      write: function (ev) {
        this.isActive2 = !this.isActive2
        if (!this.isActive2) {
          this.$message({
            message: '修改成功',
            type: 'success'
          })
        }
      }
    },
    mounted: function () {
    },
    created () {
      console.log(window.sessionStorage.index)
      Axios.get('./static/data/cinema.json')
        .then((res) => {
//          console.log(res.data.data.returnValue.mtopCinemas[window.sessionStorage.index])
          this.datas = res.data.data.returnValue.mtopCinemas[window.sessionStorage.index]
        })
    }
  }
</script>
<style>
  .chooseSat .head{
    padding-left:23px;
    text-align: left;
    width:90%;
    margin:3% 0 0 5%;
    font-size:19px;
    padding-bottom: 10px;
  }
  .chooseSat .head h5{
    font-weight:normal;
    margin:4px 0;
  }
  .chooseSat .head p{
    font-size:14px;
    color:red;
  }
  .sat{
    background:#efeff4;
    height:420px;
  }

  .buttonActive{
    position: fixed;
    bottom:15px;
    width:90%;
    margin-left:5%;
    line-height:50px;
    color:white;
    background:#ff4d64;
    font-size:18px;
  }
  .buyBtn{
    position: fixed;
    bottom:15px;
    margin-left:30%;
    bottom:25px;
  }
  .show{
    display:none;
  }
  .show2{
    display:none;
  }
  .showOne{
    position:relative;
    padding-top: 20px;
  }
  .showOne p, .showOne input{
    font-size:15px;
    margin:6px 0 10px 20px;
    text-align: left;
  }
  .showThree{
    text-align:left;
  }

  .showThree input{
    width:30%;
    line-height:28px;
    font-size:18px;
    outline-color: dodgerblue;
  }
  .showThree button{
    background:none;
    border:1px solid dodgerblue;
    border-radius:5px;
    color:dodgerblue;
    width:50px;
    line-height:30px;
    font-size:15px;
    outline: none;
  }
  .showOne img{
    width:15px;
  }

  .showOne p:nth-child(4){
    position:absolute;
    right:10px;
    bottom:10px;
    font-size:16px;
  }
  .showOne p:nth-child(4) span{
    color:red;
    font-size:20px;
    font-weight:bolder;
  }
  .showFour{
    text-align:left;
  }
  .showFour p{
    display:inline-block;
    font-size:19px;
    line-height:32px;
  }
  .seatItem>span{
    /*margin:10px 0;*/
  }
  .seatItem span span, .sat ul li span{
    background:white;
    width:31px;
    height:31px;
    border:1px solid grey;
    margin:5px 10px;
    /*margin-top:20px;*/
    display:inline-block;
    font-size:13px;
    color:white;
  }
  .sat ul{
    /*border:1px solid blue;*/
    overflow:hidden;
    /*width:68%;*/
    margin-top:15px;
    margin-left:21%;
  }
  .sat ul li{
    float:left;
  }
  .sat ul li span{
    width:20px;
    height:20px;
    float:right;
    margin:0px 10px;
  }
  .sat ul li:nth-child(2) span{
    background:red;
  }
  .sat ul li:nth-child(3) span{
    background:green;
  }
  /*.green{*/
    /*background:green;*/
  /*}*/

</style>



