</template>

<script>
  import selectCity from '@/components/selectCity/selectCity'
  import leftTop from '@/components/leftTop'
  import MovieList from '@/components/GoingList'
  export default {
    name: 'Going',
    data () {
      return {
        msg: ''
      }
    },
    components: {
      MovieList,
      selectCity,
      leftTop
    },
    created () {
      console.log(window.sessionStorage.chooseCity)
      this.city = window.sessionStorage.chooseCity
      window.sessionStorage.activeIndex = 2
      window.sessionStorage.activeIndex2 = 2
    }
  }
</script>



