<template>
  <div class="movieInfo">
    <router-link to="/"><span class="jt"><</span></router-link>
    <h2 class="sHead">{{datas.showName}}</h2>

    <div class="introduce">
      <img :src=datas.poster|imgUrl alt="">
      <div class="right">
        <h3>{{datas.showName}}</h3>
        <p>{{datas.showNameEn}}</p>
        <p>{{datas.type}}</p>
        <p>{{datas.country}}|{{datas.duration}}分钟</p>
        <p>{{datas.openTime}}在中国大陆上映</p>
        <p>想看人数</p>
        <p>{{datas.wantCount}}</p>
      </div>
    </div>
    <!--<div>{{datas.longDescription}}</div>-->
    <div class="description" >
      <!--电影描述-->
      <p v-bind:class="{ active: isActive }">电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述电影描述</p>
      <p @click="Highter($event)" style="margin-bottom: 0;font-size:20px;color:cornflowerblue">{{msg}}</p>
    </div>

    <div class="jz">
      <h5>剧照</h5>
      <!--照片-->
      <div class="pic">
        <div id="picf" >
          <img :src=a|imgUrl alt="" v-for="a in datasPic">
        </div>

      </div>

    </div>

    <div class="pf">
      <h5>票房</h5>
      <ul>
        <li>
          <p>暂无</p>
          <span>首周票房（万）</span>
        </li>
        <li>
          <p>暂无</p>
          <span>累计票房（万）</span>
        </li>
      </ul>
      <p>查看票房详情</p>
    </div>

    <div class="rm">
      <h5>热门影评</h5>
      <p>暂无</p>
    </div>
    <router-link to="/choosesat">
      <div class="chooseSat">选座购票</div>
    </router-link>
  </div>
</template>

<script>
    import Axios from 'axios'

    export default {
      index: '',
      type: '',
      name: 'movieInfor',
      data () {
        return {
          datasPic: '',
          datas: '',
          isActive: true,
          msg: '展开'
        }
      },
      methods: {
        Highter: function (ev) {
//          console.log(ev.target)
          this.isActive = !this.isActive
          if (this.msg === '展开') {
            this.msg = '收起'
          } else {
            this.msg = '展开'
          }
        }
      },
      filters: {
        imgUrl: function (value) {
          if (!value) return ''
          value = 'https://gw.alicdn.com/' + value
          return value
        }
      },
      mounted: function () {
        this.index = this.$route.params.index
      },
      created () {
//        console.log(this.$route.params.type)
        this.type = this.$route.params.type
        // 正在热映
        if (this.type === '1') {
          Axios.get('./static/data/data.json')
            .then((res) => {
//              console.log(this.type)
//              console.log(res.data.data.returnValue[this.index].trailer)
              this.datas = res.data.data.returnValue[this.index]
              this.datasPic = res.data.data.returnValue[this.index].trailer
            })
        } else if (this.type === '2') {
          Axios.get('./static/data/begin.json')
            .then((res) => {
//              console.log(this.type)
//              console.log(res.data.data.returnValue.soonShows[this.index].trailer)
              this.datas = res.data.data.returnValue.soonShows[this.index]
              this.datasPic = res.data.data.returnValue.soonShows[this.index].trailer
            })
        }
      }
    }
</script>
<style>

  .introduce img{
    width:25%;
    padding:1% 0 0 4%;
  }
  .movieInfo h3{
    font-weight:normal;
    font-size:18px;
  }
  .introduce{
    font-family: '黑体';
    font-size:13px;
    padding-top:6%;
    background:#333333;
    text-align:left;
    overflow:hidden;
    color:#ebebeb;
  }
  .introduce p{
    margin:4px 0;
  }
  .introduce p:nth-child(2){
    margin-bottom: 20px;
  }
  .introduce p:nth-child(7){
    color:#ffa84c;
    font-size:19px;
  }
  .movieInfo .right{
    /*border:1px solid blue;*/
    width:68%;
    float:right;
  }
  .description{
    padding:10px 15px;
  }
  .description p:nth-child(1){
    font-size:13px;
    margin:10px 0;
    line-height: 23px;
    text-align: left;
  }
  .active{
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
  }
  .jz, .pf, .rm{
    /*border:1px solid blue;*/
    border-top:9px solid lightgray;
  }
  .jz h5, .pf h5, .rm h5{
    font-size:15px;
    text-align: left;
    margin:2% 0 2% 3%;
    color:dimgrey;
  }
  .movieInfo .pic{
    overflow-x:hidden;
    /*border:1px solid blue;*/
    width:94%;
    margin:0 3%;

  }
  .pic #picf{
    width:300%;
    /*border:1px solid red;*/
    height:80px;
    text-align: left;
  }
  .movieInfo .pic img{
    height:80px;
    width:auto;
  }
  .pf>p{
    padding:15px 0;
    color:cornflowerblue;
  }
  .pf ul{
    overflow:hidden;
    display:flex;
    justify-content: space-around;
  }
  .pf ul li{
    color:dimgrey;
    font-size:25px;
    /*border:1px solid blue;*/
  }
  .pf li span{
    font-size:18px;
  }
  .movieInfo .chooseSat{
    width:100%;
    line-height:50px;
    color:white;
    background:#ff4d64;
    font-size:18px;
    position:fixed;
    bottom:0;
  }
  .rm{
    margin-bottom:100px;
  }
</style>



