<template>
  <div id="main">
    <div class="header" style="height:50px">
      <selectCity></selectCity>
      <leftTop></leftTop>
    </div>

    <div class="block">
      <el-carousel height="150px">
        <el-carousel-item>
          <img style="width: 100%;" src="../assets/1.jpg" alt="">
        </el-carousel-item>
        <el-carousel-item>
          <img style="width: 100%;" src="../assets/2.jpg" alt="">
        </el-carousel-item>
      </el-carousel>
    </div>

    <!--电影列表-->
    <movie-list />
  </div>
</template>
<script>
  import selectCity from '@/components/selectCity/selectCity'
  import leftTop from '@/components/leftTop'
  import MovieList from '@/components/MovieList'
  export default {
    name: 'Home',
    data () {
      return {
        msg: 'Welcome to Your Vue.js App'
      }
    },
    components: {
      MovieList,
      selectCity,
      leftTop
    },
    created () {
      console.log(window.sessionStorage.chooseCity)
      this.city = window.sessionStorage.chooseCity
      window.sessionStorage.activeIndex = 1
      window.sessionStorage.activeIndex2 = 1
    }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  *{
    padding: 0;
    margin: 0;
  }
  a {
    color: #90b4fc;
    text-decoration: none;
  }
  /*.header{*/
    /*padding-top:0;*/
  /*}*/

  #main{
    margin-bottom: 41px;
  }
</style>



