
<template>
  <ul>
    <li v-for="(dataEle,index) in datas">
      <router-link :to="{path:'movieInfo/'+index+'/'+type}">
        <div class="GoingList">
          <img :src=dataEle.poster|imgUrl alt="">
          <div class="right">
            <ul class="content">
              <h3>{{dataEle.showName}}</h3>
              <li style="color:#fea54c;font-weight: bolder">{{dataEle.wantCount}}人想看</li>
              <li>导演：{{dataEle.director}}</li>
              <li>主演：{{dataEle.leadingRole}}</li>
            </ul>
            <router-link to="/cinema">
              <button>预售</button>
            </router-link>
          </div>
        </div>
      </router-link>
    </li>
  </ul>

</template>
<script>
  import Axios from 'axios'

  export default {
    name: 'MovieList',

    data () {
      return {
        type: 2,
        datas: ''
      }
    },
    filters: {
      imgUrl: function (value) {
        if (!value) return ''
        value = 'https://gw.alicdn.com/' + value
        return value
      }
    },
    created () {
      Axios.get('./static/data/begin.json')
        .then((res) => {
          console.log(res.data.data.returnValue.soonShows)
          this.datas = res.data.data.returnValue.soonShows
        })
    }
  }
</script>
<style>
  .GoingList{
    font-size: 13px;
    color:gray;
    overflow: hidden;
    padding-bottom:5px;
    border-bottom: 1px solid lightgray;
  }
  .GoingList img{
    padding:3% 0 0 2.5%;
    width:17%;
    float:left;
  }
  .GoingList h3{
    font-size: 13px;
    color:black;
    margin-top:10px;
    font-weight:normal;
  }
  .GoingList ul{
    padding-bottom:10px;
    margin-left:10px;
    float:left;
    text-align:left;
  }
  .GoingList ul li{
    padding-top:5px;
  }
  .GoingList ul li:nth-child(4){
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
    width:150px;
  }
  .GoingList button{
    color:red;
    margin:50px 20px 0 0 ;
    width:60px;
    height:30px;
    border-radius:7px;
    border:1px solid red;
    background:none;
    float:right;
    outline: none;
  }
  .GoingList p{
    clear:both;
    padding-top:10px;
    border-top:1px solid lightgray;
  }
  .GoingList .right{
    width:79%;
    float:right;
  }
</style>



