<template>
  <div id="xinRen">
    <router-link to="/"><span><</span></router-link>
    <h3>活动说明</h3>

    <div class="one">
      <h4>{{datas.activityTag}}</h4>
      <p>{{datas.description}}</p>
    </div>

    <div class="two">
      {{datas.longDescription}}
    </div>

  </div>
</template>

<script>
import Axios from 'axios'

export default {
  name: 'xinRen',
  data () {
    return {
      datas: ''
    }
  },
  methods: {
  },
  components: {
  },
  created () {
    Axios.get('./static/data/data.json')
      .then((res) => {
        console.log(res.data.data.returnValue[0].activities[0])
        this.datas = res.data.data.returnValue[0].activities[0]
      })
  }
}
</script>

<style>
  #xinRen{
    color:#595757;
    text-align: left;
  }
  #xinRen span{
    position:absolute;
    color:#66b1ff;
    font-size:30px;
    font-weight: bolder;
    left:10px;
    top:3px;
  }
  #xinRen h3{
    text-align: center;
    font-size:17px;
    padding: 10px 0;
    border-bottom: 1px solid gainsboro;
  }
  .one{
    border-bottom:1px solid gainsboro;
    line-height:35px;
    font-size:18px;
    padding-bottom:5px;
    margin:15px 3%;
  }
  .one h4{
    font-weight:normal;
  }
  .one p{
    font-size:15px;
  }
  .two{
    width:94%;
    margin-left:3%;
  }
  #xinRen ol{
    margin-top:15px;
  }
  #xinRen li{
    word-wrap:break-word;
    font-size:13px;
    line-height:20px;
    padding-bottom:15px;
  }
</style>



