<template>
  <div class="tcard">
    <router-link :to="{path:'/cinemaInfo/'+this.index}">
      <span class="jt"><</span>
    </router-link>

    <h2 class="sHead">淘票票银行活动</h2>

    <ul>
      <li v-for="dataEle in datas">
        <div>
          <img :src=dataEle.bigPicUrl|imgUrl alt="">
          <div class="aa">
            <p>{{dataEle.activityTitle}}</p>
            <p class="hid">名额：{{dataEle.quota}}</p>
            <p>地区：{{dataEle.area}}</p>
          </div>
          <div class="bk">办卡</div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
  import Axios from 'axios'

  export default {
    data () {
      return {
        index: '',
        datas: ''
      }
    },
    created () {
      Axios.get('./static/data/bankCard.json')
        .then((res) => {
//          console.log(this.$route.params.index)
//          console.log(res.data.data.returnValue)
          this.datas = res.data.data.returnValue
          this.index = this.$route.params.index
        })
    },
    filters: {
      imgUrl: function (value) {
        if (!value) return ''
        value = 'https://gw.alicdn.com/' + value
        return value
      }
    }
  }
</script>

<style>
  .tcard{
    text-align: left;
    float:left;
    width:100%;
  }
  .tcard ul li div{
    overflow:hidden;
    margin:0 2%;
  }
  .tcard ul li img{
    float:left;
    width:100px;
    height:100px;
  }
  .tcard ul li .aa{
    float:left;
    /*border:1px solid red;*/
    width:50%;
    font-size:18px;
  }
  .tcard ul li .aa p:nth-child(2),.tcard ul li .aa p:nth-child(3){
    color:grey;
    font-size:15px;
  }
  .tcard ul li>div{
    /*float:left;*/
    border-bottom:1px solid gainsboro;
    line-height:25px;
    padding:15px;
  }
  .tcard ul li .bk{
    border:1px solid red;
    border-radius:3px;
    display:inline-block;
    float:right;
    margin-top:40px;
    color:red;
    width:50px;
    text-align: center;
    font-weight: bolder;
  }
</style>



