import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import me from '@/components/me'
import Home from '@/components/Home'
import Going from '@/components/Going'
import cinema from '@/components/cinema'
import movieInfo from '@/components/movieInfor.vue'
import choosesat from '@/components/choosesat.vue'
import xinRen from '@/components/xinRen.vue'
import affirmSat from '@/components/affirmSat.vue'
import cinemaInfo from '@/components/cinemaInfo.vue'
import cinMovieList from '@/components/cin_movieList.vue'
import tcard from '@/components/tcard.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: HelloWorld,
      children: [
        {
          path: '',
          component: Home
        },
        {
          path: '/going',
          component: Going
        },
        {
          path: '/movie',
          component: Home
        },
        {
          path: '/cinema',
          component: cinema
        }
      ]
    },
    {
      path: '/me',
      name: 'me',
      component: me
    },
    {
      path: '/movieInfo/:index/:type',
      name: 'movieInfo',
      component: movieInfo
    },
    {
      path: '/choosesat',
      name: '/choosesat',
      component: choosesat
    },
    {
      path: '/xinRen',
      name: '/xinRen',
      component: xinRen
    },
    {
      path: '/affirmSat',
      name: '/affirmSat',
      component: affirmSat
    },
    {
      path: '/cinemaInfo/:index',
      name: '/cinemaInfo',
      component: cinemaInfo,
      children: [
        {
          path: '',
          component: cinMovieList
        },
        {
          path: 'a',
          component: cinMovieList
        }
      ]
    },
    {
      path: '/tcard/:index',
      name: '/tcard',
      component: tcard
    }
  ]
})



