<template>
  <div class="leftTop">
    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
      <el-menu-item index="1">
        <router-link to="/">正在热映</router-link>
      </el-menu-item>
      <el-menu-item index="2">
        <router-link to="/going">即将上映</router-link>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
  export default {
    name: 'leftTop',
    data () {
      return {
        city: '',
        msg: 'Welcome to Your Vue.js App',
        activeIndex: window.sessionStorage.activeIndex,
        activeIndex2: window.sessionStorage.activeIndex2
      }
    },
    methods: {
      handleSelect (key, keyPath) {
        console.log(key, keyPath)
      }
    }
  }
</script>
<style>
  .leftTop .el-menu--horizontal {
    border-right: none;
    border-bottom: solid 1px #e6e6e6;
    display: inline-block;
  }
  .leftTop .el-menu--horizontal .el-menu-item {
    /*position:absolute;*/
    /*top:0;*/
    float:left;
    height: 49px;
    line-height: 50px;
    padding:0 2px;
    font-size:16px;
  }
  .leftTop .el-menu--horizontal {
    float: right;
  }
  .leftTop
  .leftTop .el-menu--horizontal>.el-menu-item.is-active, .el-menu--horizontal>.el-submenu.is-active .el-submenu__title {
    border-bottom: 2px solid #90b4fc;
    color: #90b4fc;
  }
  .leftTop .el-menu {
    position:absolute;
    top:0;
    right:0;
  }
</style>



