<template>
  <div class="leftTop2" @click="msg">
    <div>筛选 </div>
    <div>|</div>
    <div>搜索</div>
  </div>
</template>

<script>
  export default {
    name: 'leftTop2',
    data () {
      return {
      }
    },
    methods: {
      msg: function () {
        this.$message({
          message: '该功能尚未开启',
          type: 'warning'
        })
      }
    }
  }
</script>
<style>
  .leftTop2{
    display:flex;
    float:right;
    padding-right:5px;
    padding-top:13px;
    color:grey;
  }
  .leftTop2 div:nth-child(2){
    padding:0 10px;
  }
</style>



