<template>

  <div class="cinemaInfo">
    <router-link to="/cinema"><span class="jt"><</span></router-link>
    <h3 class="sHead">{{datas.cinemaName}}</h3>

    <div class="pOne">
      <p>地址：{{datas.address}}</p>

      <button v-for="a in datas.displaySupports">
        {{a.name}}
      </button>
<!--:to="{path:'tcard/'+index}"-->
      <router-link :to="{path:'/tcard/'+this.index}">
        <ul>
          <li>联名卡送影票</li>
          <li>申请浦发信用卡专享</li>
          <li class="jt2">></li>
        </ul>
      </router-link>

    </div>

    <div class="picLunbo">
    </div>

    <div class="pThree">
      <h5>银魂<span>8.3分</span></h5>
      <p>130分钟|喜剧|小栗旬观天将桥本环奈</p>
    </div>

      <div class="pTwo">
        <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
          <el-menu-item index="1">
            <router-link to="/cinemaInfo/a">今天09-03</router-link>
          </el-menu-item>
          <el-menu-item index="2">
            <router-link to="/cinemaInfo/a">明天09-04</router-link>
          </el-menu-item>
        </el-menu>
        <div>
          <p>新人首张特惠，名额有限抢完即止</p>
          <span class="jt2">></span>
        </div>
        <router-view/>
      </div>
  </div>

</template>
<script>
  import Axios from 'axios'

  export default {
    name: '',
    data () {
      return {
        datas: '',
        index: '',
        msg: 'Welcome to Your Vue.js App',
        activeIndex: '1',
        activeIndex2: '1'
      }
    },
    methods: {
      handleSelect (key, keyPath) {
//        console.log(key, keyPath)
      }
    },
    components: {
    },
    created () {
      this.index = this.$route.params.index
      Axios.get('./static/data/cinema.json')
        .then((res) => {
//          console.log(res.data.data.returnValue.mtopCinemas[this.index])
          this.datas = res.data.data.returnValue.mtopCinemas[this.index]
          window.sessionStorage.index = this.index
        })
    }
  }
</script>
<style>
  .cinemaInfo{
    text-align:left;
  }
  .cinemaInfo button{
    width:50px;
    outline:none;
    line-height:20px;
    background:none;
    border:1px solid #90b4fc;
    border-radius: 10px;
    color:#90b4fc;
    margin-top:4px;
  }
  .cinemaInfo .pOne{
    line-height:30px;
    font-size:14px;
    color:grey;
    margin-left:3%;
  }
  .cinemaInfo .picLunbo{
    border:1px solid blue;
  }
  .cinemaInfo .pOne ul{
    border:1px solid #90b4fc;
    border-radius:5px;
    background:#f3f9fd ;
    margin:10px 0;
    color:#90b4fc;
    font-size:14px;
    line-height:20px;
    padding-left:1%;
    position:relative;
    width:94%;
  }
  .cinemaInfo .jt2{
    color:gray;
    font-weight:bolder;
    position:absolute;
    right:10px;
    font-size:20px;
    line-height:40px;
    font-family: '黑体';
    top:0;
  }
  .cinemaInfo .el-menu--horizontal .el-menu-item {
    height: 52px;
  }
  .cinemaInfo .el-menu-item, .el-submenu__title {
    padding: 0px 10px;
    font-size: 16px;
  }
  .cinemaInfo .el-menu{
    margin-left:3%;
    width:94%;
  }
  .cinemaInfo .el-menu--horizontal>.el-menu-item.is-active, .el-menu--horizontal>.el-submenu.is-active .el-submenu__title {
    border-bottom: 2px solid #ff4d64;
    color: #ff4d64;
  }
  .cinemaInfo .pTwo>div{
    position:relative;
    line-height:40px;
    border-bottom:1px solid gainsboro;
    margin:0 3%;
  }
  .cinemaInfo .pThree{
    font-size:22px;
    border-bottom:1px solid gainsboro;
    text-align: center;
    line-height:30px;
  }
  .cinemaInfo .pThree span{
    color:#fea54c;
    font-weight:bolder;
  }
  .cinemaInfo .pThree p:nth-child(2){
    color:grey;
    font-size:15px;
  }
  .cinemaInfo .picLunbo{
    background:#4c4c4c ;
    height:100px;
  }
</style>



