<!--电影详情列表-->
<template>
  <ul>
    <li v-for="(dataEle,index) in datas">
    <router-link :to="{path:'movieInfo/'+index+'/'+type}">
        <div class="movieList">
          <img :src=dataEle.poster|imgUrl alt="">

          <div class="right">
            <ul class="content">
              <h3>{{dataEle.showName}}</h3>
              <li>
                <el-rate  v-model="value1" style="display:inline;"></el-rate>
                <span>分数</span>
              </li>
              <li>导演：{{dataEle.director}}</li>
              <li>主演：{{dataEle.leadingRole}}</li>
            </ul>
            <router-link to="/cinema">
              <button>购票</button>
            </router-link>

            <router-link to="/xinRen">
              <!-- {{dataEle.activities}}-->
              <p>
                <span style="color:#fea54c">
                  新人专享
                </span>
                | 新人首张特惠，名额有限抢完即止
              </p>
            </router-link>
          </div>
        </div>
    </router-link>
    </li>
    </ul>

</template>
<script>
  import Axios from 'axios'

  export default {
    name: 'MovieList',
    data () {
      return {
        src: 'https://gw.alicdn.com/',
        type: 1,
        datas: '',
        datas2: '',
        value1: 1,
        msg: 'Welcome to Your Vue.js App'
      }
    },
    filters: {
      imgUrl: function (value) {
        if (!value) return ''
        value = 'https://gw.alicdn.com/' + value
        return value
      }
    },
    created () {
      Axios.get('./static/data/data.json')
        .then((res) => {
          this.datas = res.data.data.returnValue
          console.log(res.data.data.returnValue[0].activities[0])
        })
    }
  }
</script>
<style>
  .movieList{
    font-size: 12px;
    color:gray;
    overflow: hidden;
    padding-bottom:5px;
    border-bottom: 1px solid lightgray;
  }
  .movieList img{
    padding:3% 0 0 2.5%;
    width:17%;
    float:left;
  }
  .movieList h3{
    font-size: 13px;
    color:black;
    margin-top:10px;
    font-weight:normal;
  }
  .movieList ul{
    padding-bottom:10px;
    margin-left:10px;
    float:left;
    text-align:left;
  }
  .movieList>ul li{
    padding-top:5px;
  }
  .movieList ul li:nth-child(4){
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
    width:150px;
  }
  .movieList button{
    color:red;
    margin:30px 20px 0 0 ;
    width:60px;
    height:30px;
    border-radius:7px;
    border:1px solid red;
    background:none;
    float:right;
    outline: none;
  }
  .movieList p{
    clear:both;
    padding-top:10px;
    border-top:1px solid lightgray;
  }
  .movieList .right{
    width:79%;
    float:left;
  }
</style>



